import React from 'react'
import { useForm } from 'react-hook-form'
import { Card, CardHeader, CardTitle, CardContent, Button, Input, Select, Textarea } from '@/components'
import type { Startup, StartupFormData } from '@/types'

interface StartupFormProps {
  onSubmit: (data: StartupFormData) => void
  isLoading?: boolean
  initialData?: Partial<Startup>
}

export const StartupForm: React.FC<StartupFormProps> = ({ 
  onSubmit, 
  isLoading = false,
  initialData 
}) => {
  const { register, handleSubmit, formState: { errors } } = useForm<StartupFormData>({
    defaultValues: initialData
      ? {
          name: initialData.name || '',
          industry: initialData.industry || '',
          stage: initialData.stage || '',
          description: initialData.description || '',
          website: initialData.website || '',
          linkedin_url: initialData.linkedin_url || ''
        }
      : undefined
  })

  return (
    <Card>
      <CardHeader>
        <CardTitle>{initialData ? 'Edit Startup' : 'Add New Startup'}</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <Input
              label="Company Name"
              error={errors.name?.message}
              {...register('name', { required: 'Company name is required' })}
            />

            <Input
              label="Industry"
              placeholder="e.g., Fintech, HealthTech, EdTech"
              error={errors.industry?.message}
              {...register('industry', { required: 'Industry is required' })}
            />

            <Select
              label="Stage"
              options={[
                { value: 'pre_seed', label: 'Pre-seed' },
                { value: 'seed', label: 'Seed' },
                { value: 'series_a', label: 'Series A' },
                { value: 'series_b', label: 'Series B' },
                { value: 'series_c', label: 'Series C' },
                { value: 'series_d_plus', label: 'Series D+' },
                { value: 'ipo', label: 'IPO' }
              ]}
              error={errors.stage?.message}
              {...register('stage', { required: 'Stage is required' })}
            />

            <Input
              label="Website"
              type="url"
              placeholder="https://example.com"
              {...register('website')}
            />

            <Input
              label="LinkedIn URL"
              type="url"
              placeholder="https://linkedin.com/company/example"
              {...register('linkedin_url')}
            />
          </div>

          <Textarea
            label="Description"
            rows={4}
            placeholder="Brief description of the company and what it does..."
            {...register('description')}
          />

          <div className="flex justify-end">
            <Button
              type="submit"
              isLoading={isLoading}
              disabled={isLoading}
            >
              {initialData ? 'Update Startup' : 'Add Startup'}
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  )
}