import React, { useState } from 'react'
import { useQuery, useMutation } from '@tanstack/react-query'
import { useForm } from 'react-hook-form'
import { Card, CardHeader, CardTitle, CardContent, Button, Input, Select } from '@/components'
import { LoadingSpinner } from '@/components/ui/LoadingSpinner'
import { comparableAPI, startupsAPI } from '@/lib/api'
import type { ComparableStartup } from '@/types'

interface ComparableFormData {
  target_startup_id: string
  limit: number
  min_similarity: number
}

export const ComparableStartups: React.FC = () => {
  const [comparableResults, setComparableResults] = useState<{
    startup_id: string
    startup_name: string
    comparables: ComparableStartup[]
    search_params: Record<string, any>
  } | null>(null)
  
  const { register, handleSubmit, formState: { errors } } = useForm<ComparableFormData>({
    defaultValues: {
      limit: 10,
      min_similarity: 0.7
    }
  })

  const { data: startupsData } = useQuery({
    queryKey: ['startups'],
    queryFn: () => startupsAPI.getStartups()
  })

  const findComparablesMutation = useMutation({
    mutationFn: ({ startupId, params }: { startupId: string, params?: { limit?: number, min_similarity?: number } }) => 
      comparableAPI.findComparableStartups(startupId, params),
    onSuccess: (data) => {
      setComparableResults(data)
    }
  })

  const onSubmit = async (data: ComparableFormData) => {
    if (!data.target_startup_id) {
      return
    }

    await findComparablesMutation.mutateAsync({
      startupId: data.target_startup_id,
      params: {
        limit: data.limit,
        min_similarity: data.min_similarity
      }
    })
  }

  const formatSimilarity = (score: number) => {
    return `${Math.round(score * 100)}%`
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold text-gray-900">Comparable Startups</h1>
        <p className="mt-1 text-sm text-gray-500">
          Find similar startups based on various criteria and characteristics
        </p>
      </div>

      {/* Search Criteria */}
      <Card>
        <CardHeader>
          <CardTitle>Search Criteria</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <Select
                label="Target Startup"
                options={[
                  { value: '', label: 'Select a startup' },
                  ...(startupsData?.startups.map(startup => ({
                    value: startup.id,
                    label: startup.name
                  })) || [])
                ]}
                error={errors.target_startup_id?.message}
                {...register('target_startup_id', { required: 'Please select a startup' })}
              />

              <Input
                label="Number of Results"
                type="number"
                min="1"
                max="50"
                error={errors.limit?.message}
                {...register('limit', { 
                  required: 'Limit is required',
                  valueAsNumber: true,
                  min: { value: 1, message: 'Minimum 1 result' },
                  max: { value: 50, message: 'Maximum 50 results' }
                })}
              />

              <Input
                label="Minimum Similarity"
                type="number"
                min="0"
                max="1"
                step="0.1"
                error={errors.min_similarity?.message}
                {...register('min_similarity', { 
                  required: 'Minimum similarity is required',
                  valueAsNumber: true,
                  min: { value: 0, message: 'Minimum 0' },
                  max: { value: 1, message: 'Maximum 1' }
                })}
              />
            </div>

            <div className="flex justify-end">
              <Button
                type="submit"
                isLoading={findComparablesMutation.isPending}
                disabled={findComparablesMutation.isPending}
              >
                Find Comparable Startups
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>

      {/* Results */}
      {comparableResults && (
        <Card>
          <CardHeader>
            <CardTitle>
              Comparable Startups for {comparableResults.startup_name} ({comparableResults.comparables.length} found)
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Company
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Industry
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Stage
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Similarity
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Description
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {comparableResults.comparables.map((startup, index) => (
                    <tr key={index} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm font-medium text-gray-900">
                          {startup.name}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {startup.industry}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {startup.stage}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {startup.similarity_score ? formatSimilarity(startup.similarity_score) : 'N/A'}
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-900 max-w-xs truncate">
                        {startup.description || 'No description available'}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium space-x-2">
                        <Button size="sm" variant="secondary">
                          View Details
                        </Button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </CardContent>
        </Card>
      )}

      {findComparablesMutation.isPending && (
        <Card>
          <CardContent>
            <div className="flex justify-center py-8">
              <LoadingSpinner />
              <span className="ml-2 text-gray-600">Searching for comparable startups...</span>
            </div>
          </CardContent>
        </Card>
      )}

      {findComparablesMutation.isError && (
        <Card>
          <CardContent>
            <div className="text-center py-8">
              <p className="text-error-600">Failed to find comparable startups. Please try again.</p>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}