import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios'
import {
  User,
  LoginRequest,
  LoginResponse,
  RegisterRequest,
  Startup,
  Analysis,
  CreateStartupRequest,
  CreateAnalysisRequest,
  BatchAnalysis,
  CreateBatchJobRequest,
  ComparableStartup,
  SystemMetric,
  Alert,
  PerformanceReport,
  SystemHealth,
  APIError,
} from '@/types'

class APIClient {
  private client: AxiosInstance

  constructor() {
    this.client = axios.create({
      baseURL: import.meta.env.VITE_API_URL || 'http://localhost:8000/api',
      timeout: 30000,
      headers: {
        'Content-Type': 'application/json',
      },
    })

    // Request interceptor to add auth token
    this.client.interceptors.request.use(
      (config) => {
        const token = localStorage.getItem('access_token')
        if (token) {
          config.headers.Authorization = `Bearer ${token}`
        }
        return config
      },
      (error) => Promise.reject(error)
    )

    // Response interceptor for error handling
    this.client.interceptors.response.use(
      (response) => response,
      (error) => {
        if (error.response?.status === 401) {
          localStorage.removeItem('access_token')
          window.location.href = '/login'
        }
        return Promise.reject(this.handleError(error))
      }
    )
  }

  private handleError(error: any): APIError {
    if (error.response?.data?.detail) {
      return {
        message: error.response.data.detail,
        code: error.response.status?.toString(),
        details: error.response.data,
      }
    }
    
    if (error.response?.data?.message) {
      return {
        message: error.response.data.message,
        code: error.response.status?.toString(),
        details: error.response.data,
      }
    }

    if (error.message) {
      return { message: error.message }
    }

    return { message: 'An unexpected error occurred' }
  }

  private async request<T>(config: AxiosRequestConfig): Promise<T> {
    const response: AxiosResponse<T> = await this.client.request(config)
    return response.data
  }

  // Authentication endpoints (mock for now since backend auth is not implemented)
  async login(credentials: LoginRequest): Promise<LoginResponse> {
    // Mock login for development
    return {
      access_token: 'mock_token_' + Date.now(),
      token_type: 'Bearer',
      user: {
        id: '1',
        email: credentials.email,
        name: 'Demo User',
        role: 'user',
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString()
      }
    }
  }

  async register(userData: RegisterRequest): Promise<LoginResponse> {
    // Mock register for development
    return {
      access_token: 'mock_token_' + Date.now(),
      token_type: 'Bearer',
      user: {
        id: '1',
        email: userData.email,
        name: userData.name,
        role: 'user',
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString()
      }
    }
  }

  async getCurrentUser(): Promise<User> {
    // Mock current user for development
    return {
      id: '1',
      email: 'demo@example.com',
      name: 'Demo User',
      role: 'user',
      created_at: new Date().toISOString(),
      updated_at: new Date().toISOString()
    }
  }

  // Startup endpoints
  async getStartups(params?: { 
    page?: number
    limit?: number
    industry?: string
    stage?: string
    search?: string
  }): Promise<{ startups: Startup[]; total: number }> {
    return this.request({
      method: 'GET',
      url: '/startups',
      params,
    })
  }

  async getStartup(id: string): Promise<Startup> {
    return this.request<Startup>({
      method: 'GET',
      url: `/startups/${id}`,
    })
  }

  async createStartup(data: CreateStartupRequest): Promise<Startup> {
    return this.request<Startup>({
      method: 'POST',
      url: '/startups',
      data,
    })
  }

  async updateStartup(id: string, data: Partial<CreateStartupRequest>): Promise<Startup> {
    return this.request<Startup>({
      method: 'PUT',
      url: `/startups/${id}`,
      data,
    })
  }

  async deleteStartup(id: string): Promise<void> {
    return this.request<void>({
      method: 'DELETE',
      url: `/startups/${id}`,
    })
  }

  // Analysis endpoints
  async getAnalyses(startupId?: string): Promise<Analysis[]> {
    if (startupId) {
      return this.request<Analysis[]>({
        method: 'GET',
        url: `/startups/${startupId}/analyses`,
      })
    }
    return this.request<Analysis[]>({
      method: 'GET',
      url: '/analyses',
    })
  }

  async getAnalysis(id: string): Promise<Analysis> {
    return this.request<Analysis>({
      method: 'GET',
      url: `/analyses/${id}`,
    })
  }

  async createAnalysis(data: CreateAnalysisRequest): Promise<{ success: boolean; analysis_id: string; status: string; results?: any }> {
    return this.request({
      method: 'POST',
      url: `/startups/${data.startup_id}/analyze`,
      data: {
        founder_linkedin: data.founder_linkedin,
        additional_links: data.additional_links ? JSON.stringify(data.additional_links) : undefined,
        ...data.options
      },
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    })
  }

  async getAnalysisProgress(id: string): Promise<{ progress: number; status: string }> {
    return this.request({
      method: 'GET',
      url: `/analyses/${id}/progress`,
    })
  }

  // Batch processing endpoints
  async getBatchJobs(): Promise<BatchAnalysis[]> {
    return this.request<BatchAnalysis[]>({
      method: 'GET',
      url: '/batch',
    })
  }

  async getBatchJob(id: string): Promise<BatchAnalysis> {
    return this.request<BatchAnalysis>({
      method: 'GET',
      url: `/batch/${id}`,
    })
  }

  async createBatchJob(data: CreateBatchJobRequest): Promise<{ job_id: string }> {
    return this.request({
      method: 'POST',
      url: '/batch',
      data,
    })
  }

  async startBatchJob(id: string): Promise<void> {
    return this.request<void>({
      method: 'POST',
      url: `/batch/${id}/start`,
    })
  }

  async cancelBatchJob(id: string): Promise<void> {
    return this.request<void>({
      method: 'POST',
      url: `/batch/${id}/cancel`,
    })
  }

  async getBatchProgress(id: string): Promise<{
    job_id: string
    total_tasks: number
    completed_tasks: number
    failed_tasks: number
    completion_percentage: number
    elapsed_time: number
    current_task?: string
  }> {
    return this.request({
      method: 'GET',
      url: `/batch/${id}/progress`,
    })
  }

  async downloadBatchResults(id: string, format: 'json' | 'csv' | 'xlsx'): Promise<Blob> {
    const response = await this.client.request({
      method: 'GET',
      url: `/batch/${id}/download`,
      params: { format },
      responseType: 'blob',
    })
    return response.data
  }

  // Comparable startups endpoints
  async findComparableStartups(startupId: string, params?: {
    limit?: number
    min_similarity?: number
  }): Promise<{
    startup_id: string
    startup_name: string
    comparables: ComparableStartup[]
    search_params: Record<string, any>
  }> {
    return this.request({
      method: 'GET',
      url: `/comparables/${startupId}`,
      params,
    })
  }

  // Monitoring endpoints
  async getSystemHealth(): Promise<SystemHealth> {
    return this.request<SystemHealth>({
      method: 'GET',
      url: '/monitoring/health',
    })
  }

  async getMetrics(params: {
    metric_name?: string
    start_time?: string
    end_time?: string
    hours?: number
  }): Promise<SystemMetric[]> {
    return this.request<SystemMetric[]>({
      method: 'GET',
      url: '/monitoring/metrics',
      params,
    })
  }

  async getMetricSummary(metricName: string, hours: number = 24): Promise<{
    metric_name: string
    period_hours: number
    count: number
    mean: number
    median: number
    min: number
    max: number
    stdev: number
    latest: number
    timestamp: string
  }> {
    return this.request({
      method: 'GET',
      url: `/monitoring/metrics/${metricName}/summary`,
      params: { hours },
    })
  }

  async getAlerts(): Promise<Alert[]> {
    return this.request<Alert[]>({
      method: 'GET',
      url: '/monitoring/alerts',
    })
  }

  async resolveAlert(id: string): Promise<void> {
    return this.request<void>({
      method: 'POST',
      url: `/monitoring/alerts/${id}/resolve`,
    })
  }

  async getPerformanceReport(hours: number = 24): Promise<PerformanceReport> {
    return this.request<PerformanceReport>({
      method: 'GET',
      url: '/monitoring/report',
      params: { hours },
    })
  }

  // File upload
  async uploadFile(file: File, type: 'pitch_deck' | 'batch_csv'): Promise<{ url: string }> {
    const formData = new FormData()
    formData.append('file', file)
    formData.append('type', type)

    return this.request({
      method: 'POST',
      url: '/upload',
      data: formData,
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    })
  }
}

// Create API client instance
const apiClient = new APIClient()

// Export API modules
export const authAPI = {
  login: apiClient.login.bind(apiClient),
  register: apiClient.register.bind(apiClient),
  getCurrentUser: apiClient.getCurrentUser.bind(apiClient),
}

export const startupsAPI = {
  getStartups: apiClient.getStartups.bind(apiClient),
  getStartup: apiClient.getStartup.bind(apiClient),
  createStartup: apiClient.createStartup.bind(apiClient),
  updateStartup: apiClient.updateStartup.bind(apiClient),
  deleteStartup: apiClient.deleteStartup.bind(apiClient),
}

export const analysesAPI = {
  getAnalyses: apiClient.getAnalyses.bind(apiClient),
  getAnalysis: apiClient.getAnalysis.bind(apiClient),
  createAnalysis: apiClient.createAnalysis.bind(apiClient),
  getAnalysisProgress: apiClient.getAnalysisProgress.bind(apiClient),
}

export const batchAPI = {
  getBatchJobs: apiClient.getBatchJobs.bind(apiClient),
  getBatchJob: apiClient.getBatchJob.bind(apiClient),
  createBatchJob: apiClient.createBatchJob.bind(apiClient),
  startBatchJob: apiClient.startBatchJob.bind(apiClient),
  cancelBatchJob: apiClient.cancelBatchJob.bind(apiClient),
  getBatchProgress: apiClient.getBatchProgress.bind(apiClient),
  downloadBatchResults: apiClient.downloadBatchResults.bind(apiClient),
}

export const comparableAPI = {
  findComparableStartups: apiClient.findComparableStartups.bind(apiClient),
}

export const monitoringAPI = {
  getSystemHealth: apiClient.getSystemHealth.bind(apiClient),
  getMetrics: apiClient.getMetrics.bind(apiClient),
  getMetricSummary: apiClient.getMetricSummary.bind(apiClient),
  getAlerts: apiClient.getAlerts.bind(apiClient),
  resolveAlert: apiClient.resolveAlert.bind(apiClient),
  getPerformanceReport: apiClient.getPerformanceReport.bind(apiClient),
}

export const uploadAPI = {
  uploadFile: apiClient.uploadFile.bind(apiClient),
}

export default apiClient