import React from 'react'
import { Card, CardHeader, CardTitle, CardContent } from '@/components'
import { LoadingSpinner } from '@/components/ui/LoadingSpinner'
import type { Analysis } from '@/types'

interface AnalysisProgressProps {
  analysis: Analysis
  onCancel?: () => void
}

export const AnalysisProgress: React.FC<AnalysisProgressProps> = ({ 
  analysis, 
  onCancel 
}) => {
  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed':
        return 'text-success-600 bg-success-100'
      case 'failed':
        return 'text-error-600 bg-error-100'
      case 'in_progress':
        return 'text-primary-600 bg-primary-100'
      default:
        return 'text-gray-600 bg-gray-100'
    }
  }

  const getProgressPercentage = () => {
    switch (analysis.status) {
      case 'pending':
        return 0
      case 'in_progress':
        return 50 // Could be made more sophisticated with actual progress
      case 'completed':
        return 100
      case 'failed':
        return 0
      default:
        return 0
    }
  }

  const getProgressMessage = () => {
    switch (analysis.status) {
      case 'pending':
        return 'Analysis queued for processing...'
      case 'in_progress':
        return 'Analyzing startup data...'
      case 'completed':
        return 'Analysis completed successfully!'
      case 'failed':
        return 'Analysis failed. Please try again.'
      default:
        return 'Unknown status'
    }
  }

  const progress = getProgressPercentage()

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center justify-between">
          <span>Analysis Progress</span>
          <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusColor(analysis.status)}`}>
            {analysis.status.replace('_', ' ').toUpperCase()}
          </span>
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {/* Progress Bar */}
          <div>
            <div className="flex justify-between text-sm text-gray-700 mb-2">
              <span>{getProgressMessage()}</span>
              <span>{progress}%</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-2">
              <div 
                className={`h-2 rounded-full transition-all duration-300 ${
                  analysis.status === 'completed' ? 'bg-success-600' :
                  analysis.status === 'failed' ? 'bg-error-600' :
                  'bg-primary-600'
                }`}
                style={{ width: `${progress}%` }}
              />
            </div>
          </div>

          {/* Loading Spinner for In Progress */}
          {analysis.status === 'in_progress' && (
            <div className="flex items-center justify-center py-4">
              <LoadingSpinner />
              <span className="ml-2 text-gray-600">Processing your startup analysis...</span>
            </div>
          )}

          {/* Analysis Details */}
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <span className="text-gray-500">Started:</span>
              <span className="ml-2 text-gray-900">
                {analysis.created_at 
                  ? new Date(analysis.created_at).toLocaleString()
                  : 'N/A'
                }
              </span>
            </div>
            {analysis.completed_at && (
              <div>
                <span className="text-gray-500">Completed:</span>
                <span className="ml-2 text-gray-900">
                  {new Date(analysis.completed_at).toLocaleString()}
                </span>
              </div>
            )}
          </div>

          {/* Processing Time */}
          {analysis.processing_time && (
            <div className="text-sm text-gray-500">
              Processing time: {Math.round(analysis.processing_time)} seconds
            </div>
          )}

          {/* Cancel Button */}
          {(analysis.status === 'pending' || analysis.status === 'in_progress') && onCancel && (
            <div className="flex justify-end pt-2 border-t">
              <button
                onClick={onCancel}
                className="text-sm text-error-600 hover:text-error-700 font-medium"
              >
                Cancel Analysis
              </button>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  )
}