import React, { useEffect } from 'react'
import { useQuery } from '@tanstack/react-query'
import { Card, CardHeader, CardTitle, CardContent } from '@/components'
import { LoadingSpinner } from '@/components/ui/LoadingSpinner'
import { monitoringAPI } from '@/lib/api'

export const Monitoring: React.FC = () => {
  const { data: systemHealth, isLoading: healthLoading, refetch } = useQuery({
    queryKey: ['system-health'],
    queryFn: () => monitoringAPI.getSystemHealth(),
    refetchInterval: 30000, // Refetch every 30 seconds
  })

  // Auto-refresh every 30 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      refetch()
    }, 30000)

    return () => clearInterval(interval)
  }, [refetch])

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'healthy':
      case 'active':
      case 'running':
        return 'text-success-600 bg-success-100'
      case 'degraded':
      case 'warning':
        return 'text-warning-600 bg-warning-100'
      case 'unhealthy':
      case 'error':
      case 'failed':
        return 'text-error-600 bg-error-100'
      default:
        return 'text-gray-600 bg-gray-100'
    }
  }

  if (healthLoading) {
    return (
      <div className="flex justify-center items-center h-64">
        <LoadingSpinner />
        <span className="ml-2 text-gray-600">Loading system health...</span>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">System Monitoring</h1>
          <p className="mt-1 text-sm text-gray-500">
            Real-time system health and performance metrics
          </p>
        </div>
        <div className="text-sm text-gray-500">
          Last updated: {new Date().toLocaleTimeString()}
        </div>
      </div>

      {/* System Overview */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                  systemHealth?.status === 'healthy' ? 'bg-success-100' : 'bg-error-100'
                }`}>
                  <div className={`w-3 h-3 rounded-full ${
                    systemHealth?.status === 'healthy' ? 'bg-success-600' : 'bg-error-600'
                  }`} />
                </div>
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">System Status</p>
                <p className="text-2xl font-semibold text-gray-900">
                  {systemHealth?.status || 'Unknown'}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <div className="w-8 h-8 rounded-full bg-primary-100 flex items-center justify-center">
                  <div className="w-3 h-3 rounded-full bg-primary-600" />
                </div>
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">Health Score</p>
                <p className="text-2xl font-semibold text-gray-900">
                  {systemHealth?.health_score ? `${Math.round(systemHealth.health_score * 100)}%` : 'N/A'}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <div className="w-8 h-8 rounded-full bg-warning-100 flex items-center justify-center">
                  <div className="w-3 h-3 rounded-full bg-warning-600" />
                </div>
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">Active Alerts</p>
                <p className="text-2xl font-semibold text-gray-900">
                  {systemHealth?.active_alerts || 0}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <div className="w-8 h-8 rounded-full bg-info-100 flex items-center justify-center">
                  <div className="w-3 h-3 rounded-full bg-info-600" />
                </div>
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">Last Update</p>
                <p className="text-sm font-semibold text-gray-900">
                  {systemHealth?.last_update ? new Date(systemHealth.last_update).toLocaleTimeString() : 'N/A'}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Recent Metrics */}
      <Card>
        <CardHeader>
          <CardTitle>Recent Metrics</CardTitle>
        </CardHeader>
        <CardContent>
          {systemHealth?.recent_metrics ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {Object.entries(systemHealth.recent_metrics).map(([metricName, metricValue]: [string, any]) => (
                <div key={metricName} className="border rounded-lg p-4">
                  <div className="flex items-center justify-between mb-2">
                    <h3 className="text-sm font-medium text-gray-900 capitalize">
                      {metricName.replace(/_/g, ' ')}
                    </h3>
                    <span className="text-lg font-semibold text-gray-900">
                      {typeof metricValue === 'number' 
                        ? metricValue.toLocaleString()
                        : String(metricValue)
                      }
                    </span>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <p className="text-gray-500">No metrics data available</p>
          )}
        </CardContent>
      </Card>

      {/* System Information */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>System Status Details</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-gray-500">Overall Status</span>
                <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusColor(systemHealth?.status || 'unknown')}`}>
                  {systemHealth?.status || 'Unknown'}
                </span>
              </div>
              
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-gray-500">Health Score</span>
                <div className="flex items-center space-x-2">
                  <div className="w-32 bg-gray-200 rounded-full h-2">
                    <div 
                      className="bg-primary-600 h-2 rounded-full" 
                      style={{ width: `${(systemHealth?.health_score || 0) * 100}%` }}
                    />
                  </div>
                  <span className="text-sm text-gray-900">
                    {systemHealth?.health_score ? `${Math.round(systemHealth.health_score * 100)}%` : '0%'}
                  </span>
                </div>
              </div>

              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-gray-500">Active Alerts</span>
                <span className="text-sm text-gray-900">
                  {systemHealth?.active_alerts || 0}
                </span>
              </div>

              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-gray-500">Last Updated</span>
                <span className="text-sm text-gray-900">
                  {systemHealth?.last_update 
                    ? new Date(systemHealth.last_update).toLocaleString()
                    : 'Never'
                  }
                </span>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>System Information</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4 text-sm">
              <div className="flex justify-between">
                <span className="text-gray-500">Environment</span>
                <span className="text-gray-900">Production</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Version</span>
                <span className="text-gray-900">1.0.0</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Platform</span>
                <span className="text-gray-900">Docker</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Database</span>
                <span className="text-gray-900">PostgreSQL</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Cache</span>
                <span className="text-gray-900">Redis</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Queue</span>
                <span className="text-gray-900">Celery</span>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}