import React, { useState } from 'react'
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { useForm } from 'react-hook-form'
import { Card, CardHeader, CardTitle, CardContent, Button, Input, Select, Textarea } from '@/components'
import { LoadingSpinner } from '@/components/ui/LoadingSpinner'
import { batchAPI } from '@/lib/api'
import type { CreateBatchJobRequest, BatchAnalysis } from '@/types'

interface BatchFormData {
  name: string
  processing_mode: 'fast' | 'comprehensive' | 'custom'
  description?: string
  notify_on_completion: boolean
  notification_email: string
}

export const BatchProcessing: React.FC = () => {
  const [selectedFile, setSelectedFile] = useState<File | null>(null)
  const queryClient = useQueryClient()

  const { register, handleSubmit, formState: { errors }, reset } = useForm<BatchFormData>()

  const { data: batches, isLoading: batchesLoading } = useQuery({
    queryKey: ['batches'],
    queryFn: () => batchAPI.getBatchJobs()
  })

  const createBatchMutation = useMutation({
    mutationFn: (data: CreateBatchJobRequest) => 
      batchAPI.createBatchJob(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['batches'] })
      reset()
      setSelectedFile(null)
    }
  })

  const onSubmit = async (data: BatchFormData) => {
    const requestData: CreateBatchJobRequest = {
      startup_ids: [], // Will be populated from file or manual entry
      job_name: data.name,
      config: {
        analysis_options: {
          include_web_scraping: data.processing_mode === 'comprehensive',
          include_ml_prediction: true,
          include_similarity_analysis: data.processing_mode !== 'fast'
        },
        priority: 'normal',
        max_concurrent_tasks: 5,
        timeout_per_task: 300,
        retry_failed_tasks: true,
        max_retries: 3,
        export_format: 'json',
        include_raw_data: true,
        notify_on_completion: data.notify_on_completion,
        notification_email: data.notification_email
      }
    }
    
    await createBatchMutation.mutateAsync(requestData)
  }

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      setSelectedFile(file)
    }
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed': return 'text-success-600 bg-success-100'
      case 'failed': return 'text-error-600 bg-error-100'
      case 'processing': return 'text-warning-600 bg-warning-100'
      default: return 'text-gray-600 bg-gray-100'
    }
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold text-gray-900">Batch Processing</h1>
        <p className="mt-1 text-sm text-gray-500">
          Process multiple startups at once using CSV upload or bulk data entry
        </p>
      </div>

      {/* Create New Batch */}
      <Card>
        <CardHeader>
          <CardTitle>Create New Batch</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <Input
                label="Batch Name"
                error={errors.name?.message}
                {...register('name', { required: 'Batch name is required' })}
              />

              <Select
                label="Processing Mode"
                options={[
                  { value: 'fast', label: 'Fast (Basic analysis)' },
                  { value: 'comprehensive', label: 'Comprehensive (Full analysis)' },
                  { value: 'custom', label: 'Custom Configuration' }
                ]}
                error={errors.processing_mode?.message}
                {...register('processing_mode', { required: 'Processing mode is required' })}
              />
            </div>

            <Textarea
              label="Description (Optional)"
              rows={3}
              {...register('description')}
            />

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Upload CSV File (Optional)
              </label>
              <input
                type="file"
                accept=".csv,.xlsx,.xls"
                onChange={handleFileChange}
                className="block w-full text-sm text-gray-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-primary-50 file:text-primary-700 hover:file:bg-primary-100"
              />
              {selectedFile && (
                <p className="mt-2 text-sm text-gray-600">
                  Selected: {selectedFile.name} ({(selectedFile.size / 1024).toFixed(1)} KB)
                </p>
              )}
            </div>

            <div className="flex justify-end">
              <Button
                type="submit"
                isLoading={createBatchMutation.isPending}
                disabled={createBatchMutation.isPending}
              >
                Start Batch Processing
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>

      {/* Batch History */}
      <Card>
        <CardHeader>
          <CardTitle>Batch History</CardTitle>
        </CardHeader>
        <CardContent>
          {batchesLoading ? (
            <div className="flex justify-center py-8">
              <LoadingSpinner />
            </div>
          ) : batches && batches.length > 0 ? (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Name
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Status
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Items
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Progress
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Created
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {batches.map((batch: BatchAnalysis) => (
                    <tr key={batch.batch_id}>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {batch.config.job_name || 'Unnamed Batch'}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusColor(batch.status)}`}>
                          {batch.status}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {batch.total_count}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {batch.total_count > 0 ? Math.round((batch.completed_count / batch.total_count) * 100) : 0}%
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {formatDate(batch.created_at)}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium space-x-2">
                        <Button size="sm" variant="secondary">
                          View Details
                        </Button>
                        {batch.status === 'completed' && (
                          <Button size="sm" variant="secondary">
                            Download Results
                          </Button>
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <div className="text-center py-8">
              <p className="text-gray-500">No batch processing history yet.</p>
              <p className="text-sm text-gray-400 mt-1">Create your first batch to get started.</p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}