import { useState } from 'react'
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { PlusIcon, ChartBarIcon, ClockIcon } from '@heroicons/react/24/outline'
import { startupsAPI, analysesAPI } from '@/lib/api'
import { CreateStartupRequest, StartupFormData, Startup, Analysis, CreateAnalysisRequest } from '@/types'
import { StartupForm } from '@/components/forms/StartupForm'
import { AnalysisResults } from '@/components/analysis/AnalysisResults'
import { AnalysisProgress } from '@/components/analysis/AnalysisProgress'
import { StartupList } from '@/components/startup/StartupList'
import { 
  Button, Modal, Card, LoadingSpinner, EmptyState
} from '@/components'
import toast from 'react-hot-toast'

export const Dashboard = () => {
  const [selectedStartup, setSelectedStartup] = useState<Startup | null>(null)
  const [showStartupForm, setShowStartupForm] = useState(false)

  const queryClient = useQueryClient()

  // Fetch startups
  const { 
    data: startupsData, 
    isLoading: startupsLoading 
  } = useQuery({
    queryKey: ['startups'],
    queryFn: () => startupsAPI.getStartups(),
    staleTime: 5 * 60 * 1000 // 5 minutes
  })

  // Fetch analyses for selected startup
  const { data: analyses = [] } = useQuery({
    queryKey: ['analyses', selectedStartup?.id],
    queryFn: () => selectedStartup ? analysesAPI.getAnalyses(selectedStartup.id) : Promise.resolve([]),
    enabled: !!selectedStartup?.id,
    staleTime: 2 * 60 * 1000 // 2 minutes
  })

  // Create startup mutation
  const createStartupMutation = useMutation({
    mutationFn: (data: CreateStartupRequest) => startupsAPI.createStartup(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['startups'] })
      setShowStartupForm(false)
      toast.success('Startup added successfully!')
    },
    onError: (error: any) => {
      toast.error(error?.message || 'Failed to add startup')
    }
  })

  // Create analysis mutation
  const createAnalysisMutation = useMutation({
    mutationFn: (data: CreateAnalysisRequest) => analysesAPI.createAnalysis(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['analyses', selectedStartup?.id] })
      toast.success('Analysis started successfully!')
    },
    onError: (error: any) => {
      toast.error(error?.message || 'Failed to start analysis')
    }
  })

  const handleStartupSubmit = async (formData: StartupFormData) => {
    const startupData: CreateStartupRequest = {
      name: formData.name,
      industry: formData.industry,
      stage: formData.stage,
      description: formData.description,
      website: formData.website,
      linkedin_url: formData.linkedin_url
    }

    createStartupMutation.mutate(startupData)
  }

  const handleStartAnalysis = (startup: Startup) => {
    const analysisData: CreateAnalysisRequest = {
      startup_id: startup.id,
      options: {
        include_web_scraping: true,
        include_ml_prediction: true,
        include_similarity_analysis: true,
        priority: 'normal'
      }
    }

    createAnalysisMutation.mutate(analysisData)
  }

  const handleView = (startup: Startup) => {
    setSelectedStartup(startup)
  }

  // Helper functions
  const getCompletedAnalyses = () => {
    return analyses
      .filter((a: Analysis) => a.status === 'completed')
      .sort((a: Analysis, b: Analysis) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime())
  }

  const getActiveAnalyses = () => {
    return analyses.filter((a: Analysis) => ['pending', 'in_progress'].includes(a.status))
  }

  const completedAnalyses = getCompletedAnalyses()
  const activeAnalyses = getActiveAnalyses()

  const startups = startupsData?.startups || []

  if (startupsLoading) {
    return (
      <div className="flex justify-center items-center h-64">
        <LoadingSpinner />
        <span className="ml-2 text-gray-600">Loading dashboard...</span>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Dashboard</h1>
          <p className="mt-1 text-sm text-gray-500">
            Manage your startups and track analysis results
          </p>
        </div>
        <Button
          onClick={() => setShowStartupForm(true)}
          className="inline-flex items-center"
        >
          <PlusIcon className="h-5 w-5 mr-2" />
          Add Startup
        </Button>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card className="p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <ChartBarIcon className="h-8 w-8 text-primary-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Total Startups</p>
              <p className="text-2xl font-semibold text-gray-900">{startups.length}</p>
            </div>
          </div>
        </Card>

        <Card className="p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <ClockIcon className="h-8 w-8 text-warning-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Active Analyses</p>
              <p className="text-2xl font-semibold text-gray-900">{activeAnalyses.length}</p>
            </div>
          </div>
        </Card>

        <Card className="p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <ChartBarIcon className="h-8 w-8 text-success-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Completed Analyses</p>
              <p className="text-2xl font-semibold text-gray-900">{completedAnalyses.length}</p>
            </div>
          </div>
        </Card>
      </div>

      {/* Main Content */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Startups List */}
        <div>
          <h2 className="text-lg font-medium text-gray-900 mb-4">Your Startups</h2>
          {startups.length > 0 ? (
            <StartupList
              startups={startups}
              onView={handleView}
              onAnalyze={handleStartAnalysis}
            />
          ) : (
            <Card className="p-12">
              <EmptyState
                message="No startups yet"
                description="Get started by adding your first startup to analyze"
              />
              <div className="mt-6 flex justify-center">
                <Button
                  onClick={() => setShowStartupForm(true)}
                  className="inline-flex items-center"
                >
                  <PlusIcon className="h-5 w-5 mr-2" />
                  Add Your First Startup
                </Button>
              </div>
            </Card>
          )}
        </div>

        {/* Analysis Section */}
        <div className="space-y-6">
          {/* Active Analyses */}
          {activeAnalyses.length > 0 && (
            <div>
              <h3 className="text-lg font-medium text-gray-900 mb-4">Active Analyses</h3>
              <div className="space-y-4">
                {activeAnalyses.map((analysis) => (
                  <AnalysisProgress
                    key={analysis.id}
                    analysis={analysis}
                  />
                ))}
              </div>
            </div>
          )}

          {/* Recent Results */}
          {completedAnalyses.length > 0 && (
            <div>
              <h3 className="text-lg font-medium text-gray-900 mb-4">Recent Results</h3>
              <div className="space-y-4">
                {completedAnalyses.slice(0, 3).map((analysis) => (
                  <AnalysisResults
                    key={analysis.id}
                    analysis={analysis}
                  />
                ))}
              </div>
            </div>
          )}

          {/* Empty State for Analysis */}
          {activeAnalyses.length === 0 && completedAnalyses.length === 0 && selectedStartup && (
            <Card className="p-8">
              <EmptyState
                message="No analyses yet"
                description="Start analyzing your startup to get insights and scores"
              />
              <div className="mt-4 flex justify-center">
                <Button onClick={() => handleStartAnalysis(selectedStartup)}>
                  Start Analysis
                </Button>
              </div>
            </Card>
          )}

          {/* No startup selected */}
          {!selectedStartup && startups.length > 0 && (
            <Card className="p-8">
              <EmptyState
                message="Select a startup"
                description="Choose a startup from the list to view its analyses"
              />
            </Card>
          )}
        </div>
      </div>

      {/* Modals */}
      <Modal
        isOpen={showStartupForm}
        onClose={() => setShowStartupForm(false)}
        title="Add New Startup"
      >
        <StartupForm
          onSubmit={handleStartupSubmit}
          isLoading={createStartupMutation.isPending}
        />
      </Modal>
    </div>
  )
}